import React from 'react';
import ReactDOM from 'react-dom';
import {Draggable, Droppable} from 'react-drag-and-drop'
import * as utils from './utils.js';

class DeckOfCards extends React.Component {

    constructor() {
        super();
        console.log('my m y', utils.newDeck());
        this.state = {};
    }

    render() {
        return (
            <div>
                <ul>
                    <Draggable type="fruit" data="banana">
                        <li>Banana</li>
                    </Draggable>
                    <Draggable type="fruit" data="apple">
                        <li>Apple</li>
                    </Draggable>
                    <Draggable type="metal" data="silver">
                        <li>Silver</li>
                    </Draggable>
                </ul>
                <Droppable
                    types={['fruit']} // <= allowed drop types
                    onDrop={this.onDrop.bind(this)}>
                    <ul className="Smoothie"></ul>
                </Droppable>
            </div>
        )
    }

}

ReactDOM.render(<DeckOfCards/>, document.getElementById('app'))


